"use client";

import Stack from "@mui/material/Stack";
import {
  Typography,
  Card,
  CardContent,
  Chip,
  Box,
  IconButton,
  Button,
} from "@mui/material";
import GitHubIcon from "@mui/icons-material/GitHub";
import Image from "next/image";
import ss1 from "../../assets/SS-1.jpg";
import ss2 from "../../assets/SS-2.jpg";
import ss3 from "../../assets/SS-3.jpg";
import ss4 from "../../assets/SS-4.jpg";
import { useEffect, useRef, useState } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination } from "swiper/modules";

import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import { ChevronLeft, ChevronRight, X } from "lucide-react";
import { createPortal } from "react-dom";

const allProjects = [
  {
    title: "StreamSphere - Video Streaming Platform",
    description:
      "A platform for uploading streaming videos with user authentication and video management as well as engagement features.",
    technologies: [
      "React.js",
      "Node.js",
      "MongoDB",
      "AWS S3",
      "Tailwind CSS",
      "Redux",
    ],
    pictures: [ss1, ss2, ss3, ss4],
    githubLink: "https://github.com/TahaHanif2424/StreamSphere.git",
  },
];

export default function Projects() {
  const headingRef = useRef<HTMLDivElement>(null);
  const cardContainerRef = useRef<HTMLDivElement>(null);
  const [isGalleryOpen, setIsGalleryOpen] = useState(false);
  const [currentImageIndex, setCurrentImageIndex] = useState(0);
  const [selectedProjectImages, setSelectedProjectImages] = useState([]);
  const [mainSwiper, setMainSwiper] = useState(null);

  const openGallery = (images: any, startIndex = 0) => {
    setSelectedProjectImages(images);
    setCurrentImageIndex(startIndex);
    setIsGalleryOpen(true);
    document.body.style.overflow = "hidden";
  };

  const closeGallery = () => {
    setIsGalleryOpen(false);
    setSelectedProjectImages([]);
    setCurrentImageIndex(0);
    document.body.style.overflow = "unset";
  };

  const nextImage = () => {
    setCurrentImageIndex((prev) =>
      prev === selectedProjectImages.length - 1 ? 0 : prev + 1
    );
  };

  const prevImage = () => {
    setCurrentImageIndex((prev) =>
      prev === 0 ? selectedProjectImages.length - 1 : prev - 1
    );
  };

  useEffect(() => {
    const handleKeyPress = (e: any) => {
      if (!isGalleryOpen) return;

      switch (e.key) {
        case "Escape":
          closeGallery();
          break;
        case "ArrowLeft":
          prevImage();
          break;
        case "ArrowRight":
          nextImage();
          break;
      }
    };

    document.addEventListener("keydown", handleKeyPress);
    return () => document.removeEventListener("keydown", handleKeyPress);
  }, [isGalleryOpen, nextImage, prevImage]);

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            entry.target.classList.add("animate-fade-in");
            observer.unobserve(entry.target);
          }
        });
      },
      {
        threshold: 0.2,
      }
    );

    if (headingRef.current) {
      observer.observe(headingRef.current);
    }
    if (cardContainerRef.current) {
      observer.observe(cardContainerRef.current);
    }

    return () => {
      if (headingRef.current) {
        observer.unobserve(headingRef.current);
      }
      if (cardContainerRef.current) {
        observer.unobserve(cardContainerRef.current);
      }
    };
  }, []);

  return (
    <>
    {isGalleryOpen && selectedProjectImages.length > 0 && (
        createPortal(
          <div className="fixed top-0 left-0 w-screen h-screen z-[10000] bg-black/95 backdrop-blur-sm">
            {/* Header with close button and counter */}
            <div className="absolute top-0 left-0 right-0 z-10 flex justify-between items-center p-4 bg-gradient-to-b from-black/50 to-transparent">
              <div className="text-white/80 text-sm font-medium">
                {currentImageIndex + 1} / {selectedProjectImages.length}
              </div>
            <button
              onClick={closeGallery}
              className="bg-black/50 backdrop-blur-sm p-2 rounded-full hover:bg-black/70 transition-colors text-white"
              aria-label="Close gallery"
            >
              <X size={20} />
            </button>
          </div>

          {/* Main image container */}
          <div className="flex items-center justify-center h-full p-4">
            <div className="relative max-w-7xl max-h-full flex items-center justify-center">
              <Image
                src={selectedProjectImages[currentImageIndex]}
                alt={`Gallery image ${currentImageIndex + 1}`}
                className="max-w-full max-h-full object-contain rounded-lg shadow-2xl"
                style={{ maxHeight: "calc(100vh - 120px)" }}
              />
            </div>
          </div>

          {/* Navigation arrows */}
          {selectedProjectImages.length > 1 && (
            <>
              <button
                onClick={prevImage}
                className="absolute left-4 top-1/2 -translate-y-1/2 bg-black/50 backdrop-blur-sm p-3 rounded-full hover:bg-black/70 transition-colors text-white shadow-lg"
                aria-label="Previous image"
              >
                <ChevronLeft size={24} />
              </button>
              <button
                onClick={nextImage}
                className="absolute right-4 top-1/2 -translate-y-1/2 bg-black/50 backdrop-blur-sm p-3 rounded-full hover:bg-black/70 transition-colors text-white shadow-lg"
                aria-label="Next image"
              >
                <ChevronRight size={24} />
              </button>
            </>
          )}

          {/* Thumbnail strip at bottom */}
          {selectedProjectImages.length > 1 && (
            <div className="absolute bottom-0 left-0 right-0 p-4 bg-gradient-to-t from-black/50 to-transparent">
              <div className="flex justify-center gap-2 overflow-x-auto pb-2">
                {selectedProjectImages.map((image, index) => (
                  <button
                    key={index}
                    onClick={() => setCurrentImageIndex(index)}
                    className={`flex-shrink-0 w-16 h-12 rounded border-2 overflow-hidden transition-all ${
                      index === currentImageIndex
                        ? "border-white shadow-md"
                        : "border-white/30 hover:border-white/60"
                    }`}
                  >
                    <Image
                      src={image}
                      alt={`Thumbnail ${index + 1}`}
                      className="w-full h-full object-cover"
                    />
                  </button>
                ))}
              </div>
            </div>
          )}

          {/* Click outside to close */}
          <div className="absolute inset-0 -z-10" onClick={closeGallery} />
        </div>,
        document.getElementById('portal') as HTMLElement)
      )}
    <Stack id="projects" width={"100%"} alignItems={"center"} spacing={3}>
      <Stack
        ref={headingRef}
        className="opacity-0"
        width={"100%"}
        alignItems="center"
        spacing={1}
      >
        <Typography
          variant="h4"
          fontWeight="bold"
          display={"flex"}
          alignItems="center"
          gap={"0.6rem"}
          color={"text.primary"}
        >
          My
          <Typography
            fontSize={{ xs: "1.25rem", lg: "2.125rem" }}
            component="span"
            color="primary.main"
          >
            Projects
          </Typography>
        </Typography>
        <Typography
          fontSize={"1.125rem"}
          variant="body2"
          color={"text.secondary"}
          textAlign="center"
          width={"75%"}
        >
          Here are some of my recent projects. Each project was carefully
          crafted with attention to detail, performance, and user experience.
        </Typography>
      </Stack>
      <Box
        ref={cardContainerRef}
        className="opacity-0"
        width={"80%"}
        paddingX={2}
      >
        <Swiper
          modules={[Navigation, Pagination]}
          spaceBetween={24}
          navigation
          pagination={{ clickable: true }}
          breakpoints={{
            640: { slidesPerView: 1 },
            768: { slidesPerView: 2 },
            1024: { slidesPerView: 3 },
          }}
          style={{ paddingBottom: "40px" }}
          onSwiper={setMainSwiper as any}
        >
          {allProjects.map((project, index) => (
            <SwiperSlide key={index}>
              <Card
                sx={{
                  height: "100%",
                  display: "flex",
                  flexDirection: "column",
                  borderRadius: 3,
                  boxShadow: 3,
                  transition: "transform 0.3s ease",
                  "&:hover": { transform: "scale(1.02)" },
                }}
              >
                <Box sx={{ position: "relative", height: 180 }}>
                  <Image
                    src={project.pictures[0]}
                    alt={project.title}
                    fill
                    style={{
                      objectFit: "cover",
                      borderTopLeftRadius: 12,
                      borderTopRightRadius: 12,
                    }}
                  />
                </Box>

                <CardContent sx={{ flexGrow: 1 }}>
                  <Typography variant="h6" fontWeight="bold" gutterBottom>
                    {project.title}
                  </Typography>

                  <Typography variant="body2" color="text.secondary" mb={2}>
                    {project.description}
                  </Typography>

                  <Box display="flex" flexWrap="wrap" gap={0.5} mb={2}>
                    {project.technologies.map((t, i) => (
                      <Chip
                        key={i}
                        label={t}
                        size="small"
                        color="primary"
                        variant="outlined"
                      />
                    ))}
                  </Box>

                  <Box mt="auto" display="flex" justifyContent="center" gap={1}>
                    <IconButton
                      href={project.githubLink}
                      target="_blank"
                      rel="noopener noreferrer"
                      color="primary"
                      aria-label="GitHub"
                    >
                      <GitHubIcon />
                    </IconButton>

                    <Button
                      variant="text"
                      size="small"
                      onClick={() => openGallery(project.pictures, 0)}
                    >
                      Gallery
                    </Button>
                  </Box>
                </CardContent>
              </Card>
            </SwiperSlide>
          ))}
        </Swiper>
      </Box>
    </Stack>
    </>
  );
}
